webpackJsonp([142629428675168],{

/***/ 382:
/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"n0の断片","description":"メモとか、スニペットとか","url":"https://tkmn0.github.io/scrap","author":"n0","twitter":"n0","adsense":""}},"remark":{"posts":[{"post":{"html":"<p>このブログは Gatsby というフレームワークによって書かれています。</p>\n<!--more-->\n<p>Gatsby を髪の毛以外に使えるとは思いませんでしたねぇ。<br>\nGatsby: <a href=\"https://github.com/gatsbyjs/gatsby\">https://github.com/gatsbyjs/gatsby</a><br>\nDocument: <a href=\"https://www.gatsbyjs.org/docs/\">https://www.gatsbyjs.org/docs/</a></p>\n<p>Document の通りすすめていけば簡単に React.js で書かれたページが作成されます。 もしくは,“Gatsby react”とかって検索していただければ日本語の記事も複数出てきます。(このページは@jaxx2104 さん作と思われるテンプレートが Gatsby の Document に乗ってる starter に含まれていたので、存分に使わせていただいております。こんなかっこいいテンプレート誠にありがとうございます。)</p>\n<p>記事の作成はマークダウン方式でガシガシ作成します。リアルタイムで記事内容が反映されていくので、書いてて楽しいですね笑。</p>\n<h2>ブログやること</h2>\n<hr>\n<ul>\n<li>コマンドで記事作成できるようにする</li>\n<li><del>コマンドで記事を投稿できるようにする</del></li>\n<li>右側に記事のカテゴリ別一覧とかを表示する</li>\n<li>前の記事、次の記事　ボタンの作成</li>\n<li>各種リンク(github, twitter)を作る(フッターかな？)</li>\n</ul>\n<p>ただ、React.js も scss も詳しくないので、時間がかかるかもなあ、というのが本音ですね。頑張っていきましょう。</p>","frontmatter":{"layout":"post","title":"Gatsbyのご紹介。","path":"/gatsby01/","categories":["ブログ"],"date":"2018/01/06"}}},{"post":{"html":"<p>Hotkey.app のご紹介です。</p>\n<!--more-->\n<p>最近は、自分のプログラミングの領域が定まってきたのか、使うツールが決まったものになってきました。</p>\n<ul>\n<li>Xcde(iOS)</li>\n<li>Android Studio</li>\n<li>Unity(iOS,Android)</li>\n<li>VisualCode Studio(editor)</li>\n<li>Chrome(Browser)</li>\n<li>Boostnote(memo)</li>\n<li>iterm(CLI)  </li>\n</ul>\n<p>使うアプリが決まってたら、Cmd+tab でいちいちアプリを切り替えるより、ショートカットキーで使いたいアプリを最前面に持ってこれたらいいなぁと思っていました。<del>そもそも Cmd+tab の UI,UX があまり好きではないのです</del><br>\nそこで今回使用したアプリが、<a href=\"https://itunes.apple.com/jp/app/hotkey-app/id975890633?mt=12\">HotKey.app</a>です。そのままですね。<br>\n使い方もとても簡単なので、ほぼ脳死で出来上がります。<br>\n僕の場合は、 </p>\n<ul>\n<li>iterm => Cmd+shift+t</li>\n<li>vscode => Cmd+shit+v</li>\n<li>Chrome => Cmd+shift+c</li>\n<li>Boostnote => Cmd+shift+b    </li>\n</ul>\n<p>と設定しております。 Unity はバージョンを複数管理しているので、それぞれにコマンド振るのもなんだかなぁと思ったので、、、。 Xcode,AndroidStudio については割り振っても問題ないのですが、使用頻度的に、とりあえず SpotLight でいいかなと\n<br>\n最初は、applescript とか、automator を使って自作しようとしたのですが、案外面倒臭かったのでやめました笑。\n車輪の再なんとかですしね。<br>\n発見としては、applescript が javascript で書けるらしいのです！これは驚いた。<br>\nいつか機会があったら、automator やってみようかなあ　くらいの感じです。<br>\nではまた次回</p>","frontmatter":{"layout":"post","title":"Hotkeyがあつい","path":"/hotkey/","categories":["効率化"],"date":"2018/01/08"}}},{"post":{"html":"<p>技術ブログを始めることになりました。</p>\n<!--more-->\n<p>技術ブログといっても、メモレベルのことばかりになるやもしれませんが。\nちなみに、Gatsby という静的 web ページ制作のためのフレームワークを使用しました。React.js で動いています。どうですかー？ロード速いですか？</p>\n<p>これからページをいじっていこうと思っています。\nReact.js と、scss を勉強します。</p>","frontmatter":{"layout":"post","title":"TECH Blog 始めます","path":"/Hello_World/","categories":["雑記"],"date":"2018/01/06"}}},{"post":{"html":"<p>コマンドで記事のテンプレート作成まで行けたら、と思ってもがいてみたので、その備忘録をば。</p>\n<h3>ゴール</h3>\n<p><code>npm run blog -- -w タイトル パス</code>  </p>\n<p>このコマンドでテンプレートを作成する。</p>\n<!--more-->\n<h3>やったこと</h3>\n<ul>\n<li>\n<ol>\n<li>shellscript を書く</li>\n</ol>\n</li>\n<li>\n<ol start=\"2\">\n<li>package.json の中身をいじる</li>\n</ol>\n</li>\n</ul>\n<h2>1. shellscriptを書く</h2>\n<hr>\n<p>blog.sh を作成</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-powershell\"><code><span class=\"token comment\">#blog.sh</span>\n<span class=\"token comment\">#!/bin/bash</span>\n<span class=\"token keyword\">function</span> usage<span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n<span class=\"token function\">cat</span> &lt;&lt;_EOT_\nUsage:\n  <span class=\"token variable\">$0</span> <span class=\"token punctuation\">[</span><span class=\"token operator\">-</span>w<span class=\"token punctuation\">]</span> <span class=\"token punctuation\">[</span><span class=\"token operator\">-</span>p<span class=\"token punctuation\">]</span> <span class=\"token punctuation\">[</span><span class=\"token operator\">-</span>f filename<span class=\"token punctuation\">]</span> arg1 <span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span>\n\nDescription:\n  hogehogehoge\n\nOptions:\n  <span class=\"token operator\">-</span>w    <span class=\"token function\">write</span> blog<span class=\"token punctuation\">(</span>create blog componets<span class=\"token punctuation\">)</span>\n  <span class=\"token operator\">-</span>p    post blog to github pages\n\n_EOT_\n<span class=\"token keyword\">exit</span> 1\n<span class=\"token punctuation\">}</span>\n<span class=\"token keyword\">function</span> <span class=\"token function\">write</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  local today=$<span class=\"token punctuation\">(</span>date <span class=\"token string\">\"+%m%d%Y\"</span><span class=\"token punctuation\">)</span>\n  <span class=\"token keyword\">if</span> <span class=\"token punctuation\">[</span> <span class=\"token operator\">!</span> <span class=\"token operator\">-</span>e <span class=\"token string\">\"./src/pages/articles/${today}\"</span> <span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span> then\n  mkdir <span class=\"token string\">\"./src/pages/articles/${today}\"</span>\n  fi\n\n  local title=<span class=\"token string\">\"<span class=\"token variable\">$1</span>\"</span>\n  local path=<span class=\"token string\">\"./src/pages/articles/${today}/<span class=\"token variable\">$title</span>\"</span>\n  local url=<span class=\"token string\">\"#\"</span>\n  <span class=\"token keyword\">if</span> <span class=\"token punctuation\">[</span> <span class=\"token operator\">-</span>n <span class=\"token string\">\"<span class=\"token variable\">$2</span>\"</span> <span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>then\n  url=<span class=\"token string\">\"<span class=\"token variable\">$2</span>\"</span>\n  fi\n  <span class=\"token keyword\">if</span> <span class=\"token punctuation\">[</span> <span class=\"token operator\">-</span>e <span class=\"token variable\">$path</span> <span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span> then\n  local num=1\n  local _path=<span class=\"token variable\">$path</span><span class=\"token string\">\"(${num})\"</span>\n  <span class=\"token keyword\">while</span> <span class=\"token punctuation\">[</span> <span class=\"token operator\">-</span>e <span class=\"token variable\">$_path</span> <span class=\"token punctuation\">]</span>\n  <span class=\"token keyword\">do</span> \n    num=$<span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span> num <span class=\"token operator\">+</span> 1 <span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n    _path=<span class=\"token variable\">$path</span><span class=\"token string\">\"(${num})\"</span>\n  done\n  <span class=\"token function\">echo</span> <span class=\"token string\">\"the directry ${today} is already exist!\"</span>\n  <span class=\"token function\">echo</span> <span class=\"token string\">\"create (<span class=\"token variable\">$num</span>) directry\"</span>\n  path=<span class=\"token variable\">$_path</span>\n  title<span class=\"token operator\">+=</span><span class=\"token string\">\"(${num})\"</span>\n  url<span class=\"token operator\">+=</span><span class=\"token string\">\"<span class=\"token variable\">$num</span>\"</span>\n  fi\n  mkdir <span class=\"token variable\">$path</span>\n\n  local indexpath=<span class=\"token string\">\"<span class=\"token variable\">$path</span>/index.md\"</span>\n  touch <span class=\"token variable\">$indexpath</span>\n  <span class=\"token function\">echo</span> <span class=\"token operator\">--</span><span class=\"token operator\">-</span> > <span class=\"token variable\">$indexpath</span>\n  <span class=\"token function\">echo</span> title: <span class=\"token variable\">$title</span> >> <span class=\"token variable\">$indexpath</span>\n  <span class=\"token function\">echo</span> date: \\<span class=\"token string\">\"$(date \"</span>+<span class=\"token operator\">%</span>Y-<span class=\"token operator\">%</span>m-<span class=\"token operator\">%</span>dT<span class=\"token operator\">%</span>H:<span class=\"token operator\">%</span>M:<span class=\"token operator\">%</span>S<span class=\"token punctuation\">.</span>121Z<span class=\"token string\">\")\\\"</span> >> <span class=\"token variable\">$indexpath</span>\n  <span class=\"token function\">echo</span> layout: post >> <span class=\"token variable\">$indexpath</span>\n  <span class=\"token function\">echo</span> path: \\<span class=\"token string\">\"/<span class=\"token variable\">$today</span>/<span class=\"token variable\">$url</span>/\\\"</span> >> <span class=\"token variable\">$indexpath</span>\n  <span class=\"token function\">echo</span> categories: >> <span class=\"token variable\">$indexpath</span>\n  <span class=\"token function\">echo</span> <span class=\"token string\">\" \"</span><span class=\"token operator\">-</span><span class=\"token string\">\" \"</span>  >> <span class=\"token variable\">$indexpath</span>\n  <span class=\"token function\">echo</span> description: <span class=\"token string\">\"\"</span>  >> <span class=\"token variable\">$indexpath</span>\n  <span class=\"token function\">echo</span> <span class=\"token operator\">--</span><span class=\"token operator\">-</span> >> <span class=\"token variable\">$indexpath</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token keyword\">if</span> <span class=\"token punctuation\">[</span> <span class=\"token string\">\"<span class=\"token variable\">$OPTIND</span>\"</span> = 1 <span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span> then\n  <span class=\"token keyword\">while</span> getopts wp:h OPT\n  <span class=\"token keyword\">do</span>\n    case <span class=\"token variable\">$OPT</span> in\n      w<span class=\"token punctuation\">)</span>\n      title=<span class=\"token string\">\"\"</span>\n      <span class=\"token keyword\">if</span> <span class=\"token punctuation\">[</span> <span class=\"token operator\">-</span>n <span class=\"token string\">\"<span class=\"token variable\">$2</span>\"</span> <span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span> then\n        title=<span class=\"token variable\">$2</span> \n      <span class=\"token keyword\">else</span>\n        title=<span class=\"token string\">\"NO_TITLE\"</span>\n      fi\n      <span class=\"token function\">write</span> <span class=\"token variable\">$title</span> <span class=\"token variable\">$3</span>\n\t<span class=\"token punctuation\">;</span><span class=\"token punctuation\">;</span>\n      p<span class=\"token punctuation\">)</span> <span class=\"token function\">echo</span> <span class=\"token string\">\"post blog\"</span>\n\t<span class=\"token punctuation\">;</span><span class=\"token punctuation\">;</span>\n      h<span class=\"token punctuation\">)</span>\n        <span class=\"token function\">echo</span> <span class=\"token string\">\"h option. display help\"</span>       <span class=\"token comment\"># for debug</span>\n        usage\n        <span class=\"token punctuation\">;</span><span class=\"token punctuation\">;</span>\n      \\?<span class=\"token punctuation\">)</span>\n        <span class=\"token function\">echo</span> <span class=\"token string\">\"Try to enter the h option.\"</span> 1>&amp;2\n        <span class=\"token punctuation\">;</span><span class=\"token punctuation\">;</span>\n    esac\n  done\n<span class=\"token keyword\">else</span>\n  <span class=\"token function\">echo</span> <span class=\"token string\">\"No installed getopts-command.\"</span> 1>&amp;2\n  <span class=\"token keyword\">exit</span> 1\nfi\n</code></pre>\n      </div>\n<h3>ポイント</h3>\n<hr>\n<ul>\n<li>\n<h3>getpts + while</h3>\n<p>getopts でオプションを決めます。\n今回は、後々 -p　で post の処理を追加する予定なので、p と w の選択肢があります。<a href=\"https://qiita.com/2zk/items/80a8376f2feca02fcee2\">参考</a></p>\n</li>\n<li>\n<h3>write関数</h3>\n<p>ここで、パス、タイトルの指定ができます。\n/articles/DATE/TITLE/index.md\nとなります。\nこの時に、タイトルが被ったら\n/articles/DATE/TITLE(1)/index.md になるように while 文をぶん回してます。</p>\n</li>\n<li>\n<h3>echo hogehoge > index.md</h3>\n<p>この書き方で、“hogehoge”が index.md に書き込まれます<br>\n※“追加”の場合は > ではなく >> にする。</p>\n</li>\n<li>\n<h3>メモ</h3>\n<h4>条件式</h4>\n<ul>\n<li>空文チェック <code>[ -n \"変数\" ]</code></li>\n<li>\n<p>ディレクトリチエック <code>[ -e path ]</code></p>\n<h4>if文</h4>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-powershell\"><code><span class=\"token keyword\">if</span> <span class=\"token punctuation\">[</span>条件<span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span> then\n<span class=\"token comment\"># do something</span>\n<span class=\"token keyword\">else</span> \n<span class=\"token comment\"># do something</span>\n<span class=\"token keyword\">if</span>\n</code></pre>\n      </div>\n<h4>while文</h4>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-powershell\"><code><span class=\"token keyword\">while</span> <span class=\"token punctuation\">[</span>条件<span class=\"token punctuation\">]</span>\n<span class=\"token keyword\">do</span>\n<span class=\"token comment\"># do something </span>\ndone\n</code></pre>\n      </div>\n</li>\n</ul>\n</li>\n</ul>\n<h2>2. package.jsonをいじる</h2>\n<hr>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-json\"><code>//package.json\n...\n  <span class=\"token property\">\"scripts\"</span><span class=\"token operator\">:</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token property\">\"build\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"gatsby build\"</span><span class=\"token punctuation\">,</span>\n    <span class=\"token property\">\"deploy\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"gatsby build --prefix-paths &amp;&amp; gh-pages -d public\"</span><span class=\"token punctuation\">,</span>\n    <span class=\"token property\">\"develop\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"gatsby develop\"</span><span class=\"token punctuation\">,</span>\n    <span class=\"token property\">\"format\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"prettier --write \\\"**/*.+(js|jsx|json|css|scss)\\\"\"</span><span class=\"token punctuation\">,</span>\n    <span class=\"token property\">\"lint\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"eslint --ext .js,.jsx --ignore-pattern public .\"</span><span class=\"token punctuation\">,</span>\n    <span class=\"token property\">\"lint:text\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"textlint src/pages/**/index.md\"</span><span class=\"token punctuation\">,</span>\n    <span class=\"token property\">\"lint:textfix\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"textlint --fix src/pages/**/index.md\"</span><span class=\"token punctuation\">,</span>\n    <span class=\"token property\">\"precommit\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"lint-staged\"</span><span class=\"token punctuation\">,</span>\n    <span class=\"token property\">\"test\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"echo \\\"Error: no test specified\\\" &amp;&amp; exit 1\"</span><span class=\"token punctuation\">,</span>\n    <span class=\"token property\">\"blog\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"sh sh_scripts/blog.sh $opt $path $title\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">}</span>\n...\n</code></pre>\n      </div>\n<p>package.json の scripts の部分に書き加えることで,<br>\n<code>npm run hogehoge</code><br>\nコマンドを使えるようになります。<br>\n今回は、<code>blog</code>というコマンドを追加しました。<br>\nポイントとしては、　上記 hogehoge コマンドを引数(オプションとか)を含めて叩きたい時は  <code>npm run hogehoge -- 引数1 引数2 引数3 ..</code><br>\nとするところでしょうか。<br>\nblog コマンドが叩かれたら、sh コマンドを叩いて、引数を渡す、といった挙動になります。</p>\n<h3>所感</h3>\n<hr>\n<p>やりたいことができてよかったです。\nshellscript は文法がなかなか特殊だったので、若干苦戦しましたかね。<br>\nshellscript の中で普段使ってるコマンドを叩けるのが新鮮でした。<br>\n最初は.zshrc の中に書こうか迷ってたのですが、プロジェクト内に sh ファイルを置いて、npm のコマンドで叩けるようになったのでよかったです。</p>","frontmatter":{"layout":"post","title":"シェルスクリプトで記事のテンプレ作成","path":"/shell_script/","categories":["shellscript","効率化"],"date":"2018/01/07"}}},{"post":{"html":"<ul>\n<li>クロージャによるコールバック</li>\n<li>Delegate を使ったコールバック</li>\n<li>NotificationCenter を使ったコールバック</li>\n</ul>\n<p>今回は<code>Delegate</code>を使ったコールバックをゆるゆる紹介します。</p>\n  <!--more-->\n<h1>デリゲートとは</h1>\n<p>The Swift Programing Language によると</p>\n<blockquote>\n<p>Delegation is a design pattern that enables a class or structure to hand off(or delegate) some of its responsibilities to an instance of another type.    </p>\n</blockquote>\n<p>だそうで、デリゲートとは、</p>\n<ul>\n<li><code>デザインパンターンの一つ</code>である。</li>\n<li>delegate を用いれば、とあるクラスや構造体は、処理の一部を他の型のインスタンスに移譲することができます。   </li>\n</ul>\n<h3>デザインパターンとは</h3>\n<hr>\n<blockquote>\n<p>ソフトウェア開発におけるデザインパターン（型紙（かたがみ）または設計パターン、英: design pattern）とは、過去のソフトウェア設計者が発見し編み出した設計ノウハウを蓄積し、名前をつけ、再利用しやすいように特定の規約に従ってカタログ化したものである</p>\n</blockquote>\n<p><a href=\"https://ja.wikipedia.org/wiki/%E3%83%87%E3%82%B6%E3%82%A4%E3%83%B3%E3%83%91%E3%82%BF%E3%83%BC%E3%83%B3_(%E3%82%BD%E3%83%95%E3%83%88%E3%82%A6%E3%82%A7%E3%82%A2)\">wikipediaより引用</a></p>\n<p>なんかよくわからないですね。\n同じ wiki 内の概要がわかりやすいかったです。</p>\n<blockquote>\n<p>コンピュータのプログラミングで、素人と達人の間では驚くほどの生産性の差があり、その差はかなりの部分が経験の違いからきている。達人は、さまざまな難局を、何度も何度も耐え忍んで乗り切ってきている。そのような達人たちが同じ問題に取り組んだ場合、典型的にはみな同じパターンの解決策に辿り着く。これがデザインパターンである (GoF)。</p>\n</blockquote>\n<p><code>問題解決のパターンを共有しようよ。そしたら設計の話し合いとかスムーズにいくよね</code><br>\nということですね。</p>\n<h3>処理を移譲してみる</h3>\n<hr>\n<p>実際にコードを書いて見ましょう。</p>\n<p>まず、色を外から指定する ColorView を作ります。<br>\n</p>\n<h4>UIの準備</h4>\n<p><code>ColorView.swift</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-swift\"><code><span class=\"token keyword\">import</span> <span class=\"token builtin\">UIKit</span>\n\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">ColorView</span><span class=\"token punctuation\">:</span> <span class=\"token builtin\">UIView</span> <span class=\"token punctuation\">{</span>\n\n    <span class=\"token keyword\">enum</span> colors<span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">case</span> red\n        <span class=\"token keyword\">case</span> blue\n        <span class=\"token keyword\">case</span> yellow\n        <span class=\"token keyword\">case</span> green\n        <span class=\"token keyword\">case</span> <span class=\"token keyword\">none</span>\n    <span class=\"token punctuation\">}</span>\n    \n    <span class=\"token keyword\">var</span> background<span class=\"token punctuation\">:</span> colors<span class=\"token operator\">?</span>\n    \n    <span class=\"token keyword\">override</span> <span class=\"token keyword\">init</span><span class=\"token punctuation\">(</span>frame<span class=\"token punctuation\">:</span> <span class=\"token builtin\">CGRect</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">super</span><span class=\"token punctuation\">.</span><span class=\"token keyword\">init</span><span class=\"token punctuation\">(</span>frame<span class=\"token punctuation\">:</span> frame<span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">}</span>\n    <span class=\"token keyword\">public</span> <span class=\"token keyword\">func</span> <span class=\"token function\">setColor</span><span class=\"token punctuation\">(</span>color<span class=\"token punctuation\">:</span> colors<span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>background <span class=\"token operator\">=</span> color\n        <span class=\"token keyword\">switch</span> color <span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span>red<span class=\"token punctuation\">:</span>\n            <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>backgroundColor <span class=\"token operator\">=</span> <span class=\"token punctuation\">.</span>red\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span>blue<span class=\"token punctuation\">:</span>\n            <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>backgroundColor <span class=\"token operator\">=</span> <span class=\"token punctuation\">.</span>blue\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span>yellow<span class=\"token punctuation\">:</span>\n            <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>backgroundColor <span class=\"token operator\">=</span> <span class=\"token punctuation\">.</span>yellow\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span>green<span class=\"token punctuation\">:</span>\n            <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>backgroundColor <span class=\"token operator\">=</span> <span class=\"token punctuation\">.</span>green\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span><span class=\"token keyword\">none</span><span class=\"token punctuation\">:</span>\n            <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>backgroundColor <span class=\"token operator\">=</span> <span class=\"token punctuation\">.</span>black\n        <span class=\"token punctuation\">}</span>\n    <span class=\"token punctuation\">}</span>\n    \n    <span class=\"token keyword\">override</span> <span class=\"token keyword\">func</span> <span class=\"token function\">touchesBegan</span><span class=\"token punctuation\">(</span><span class=\"token number\">_</span> touches<span class=\"token punctuation\">:</span> <span class=\"token builtin\">Set</span><span class=\"token operator\">&lt;</span><span class=\"token builtin\">UITouch</span><span class=\"token operator\">></span><span class=\"token punctuation\">,</span> with event<span class=\"token punctuation\">:</span> <span class=\"token builtin\">UIEvent</span><span class=\"token operator\">?</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n        <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"touched\"</span><span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">}</span>\n    \n    <span class=\"token keyword\">required</span> <span class=\"token keyword\">init</span><span class=\"token operator\">?</span><span class=\"token punctuation\">(</span>coder aDecoder<span class=\"token punctuation\">:</span> <span class=\"token builtin\">NSCoder</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n        <span class=\"token function\">fatalError</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"init(coder:) has not been implemented\"</span><span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">}</span>\n    \n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p><code>ViewController.swift</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-swift\"><code><span class=\"token keyword\">import</span> <span class=\"token builtin\">UIKit</span>\n\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">ViewController</span><span class=\"token punctuation\">:</span> <span class=\"token builtin\">UIViewController</span> <span class=\"token punctuation\">{</span>\n    \n    <span class=\"token keyword\">override</span> <span class=\"token keyword\">func</span> <span class=\"token function\">viewDidLoad</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">super</span><span class=\"token punctuation\">.</span><span class=\"token function\">viewDidLoad</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">let</span> size <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>view<span class=\"token punctuation\">.</span>frame<span class=\"token punctuation\">.</span>width<span class=\"token operator\">*</span><span class=\"token number\">0.8</span><span class=\"token punctuation\">)</span><span class=\"token operator\">/</span><span class=\"token number\">2</span>\n        <span class=\"token keyword\">let</span> mergin <span class=\"token operator\">=</span> <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>view<span class=\"token punctuation\">.</span>frame<span class=\"token punctuation\">.</span>width<span class=\"token operator\">*</span><span class=\"token number\">0.1</span>\n        \n        <span class=\"token comment\">//Initialize Views</span>\n        <span class=\"token keyword\">let</span> red <span class=\"token operator\">=</span> <span class=\"token function\">ColorView</span><span class=\"token punctuation\">(</span>frame<span class=\"token punctuation\">:</span> <span class=\"token function\">CGRect</span><span class=\"token punctuation\">(</span>x<span class=\"token punctuation\">:</span> mergin<span class=\"token punctuation\">,</span> y<span class=\"token punctuation\">:</span> <span class=\"token number\">60</span><span class=\"token punctuation\">,</span> width<span class=\"token punctuation\">:</span> size<span class=\"token punctuation\">,</span> height<span class=\"token punctuation\">:</span> size<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">let</span> blue <span class=\"token operator\">=</span> <span class=\"token function\">ColorView</span><span class=\"token punctuation\">(</span>frame<span class=\"token punctuation\">:</span> <span class=\"token function\">CGRect</span><span class=\"token punctuation\">(</span>x<span class=\"token punctuation\">:</span> <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>view<span class=\"token punctuation\">.</span>frame<span class=\"token punctuation\">.</span>width<span class=\"token operator\">-</span><span class=\"token punctuation\">(</span>size<span class=\"token operator\">+</span>mergin<span class=\"token punctuation\">)</span><span class=\"token punctuation\">,</span> y<span class=\"token punctuation\">:</span> <span class=\"token number\">60</span><span class=\"token punctuation\">,</span> width<span class=\"token punctuation\">:</span> size<span class=\"token punctuation\">,</span> height<span class=\"token punctuation\">:</span> size<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">let</span> yellow <span class=\"token operator\">=</span> <span class=\"token function\">ColorView</span><span class=\"token punctuation\">(</span>frame<span class=\"token punctuation\">:</span> <span class=\"token function\">CGRect</span><span class=\"token punctuation\">(</span>x<span class=\"token punctuation\">:</span> mergin<span class=\"token punctuation\">,</span> y<span class=\"token punctuation\">:</span> <span class=\"token number\">60</span><span class=\"token operator\">+</span>size<span class=\"token punctuation\">,</span> width<span class=\"token punctuation\">:</span> size<span class=\"token punctuation\">,</span> height<span class=\"token punctuation\">:</span> size<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">let</span> green <span class=\"token operator\">=</span> <span class=\"token function\">ColorView</span><span class=\"token punctuation\">(</span>frame<span class=\"token punctuation\">:</span> <span class=\"token function\">CGRect</span><span class=\"token punctuation\">(</span>x<span class=\"token punctuation\">:</span> <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>view<span class=\"token punctuation\">.</span>frame<span class=\"token punctuation\">.</span>width<span class=\"token operator\">-</span><span class=\"token punctuation\">(</span>size<span class=\"token operator\">+</span>mergin<span class=\"token punctuation\">)</span><span class=\"token punctuation\">,</span> y<span class=\"token punctuation\">:</span> <span class=\"token number\">60</span><span class=\"token operator\">+</span>size<span class=\"token punctuation\">,</span> width<span class=\"token punctuation\">:</span> size<span class=\"token punctuation\">,</span> height<span class=\"token punctuation\">:</span> size<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n        \n        <span class=\"token comment\">//Set Color</span>\n        red<span class=\"token punctuation\">.</span><span class=\"token function\">setColor</span><span class=\"token punctuation\">(</span>color<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">.</span>red<span class=\"token punctuation\">)</span>\n        blue<span class=\"token punctuation\">.</span><span class=\"token function\">setColor</span><span class=\"token punctuation\">(</span>color<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">.</span>blue<span class=\"token punctuation\">)</span>\n        yellow<span class=\"token punctuation\">.</span><span class=\"token function\">setColor</span><span class=\"token punctuation\">(</span>color<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">.</span>yellow<span class=\"token punctuation\">)</span>\n        green<span class=\"token punctuation\">.</span><span class=\"token function\">setColor</span><span class=\"token punctuation\">(</span>color<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">.</span>green<span class=\"token punctuation\">)</span>\n        \n        <span class=\"token comment\">//Add view</span>\n        <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>view<span class=\"token punctuation\">.</span><span class=\"token function\">addSubview</span><span class=\"token punctuation\">(</span>red<span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>view<span class=\"token punctuation\">.</span><span class=\"token function\">addSubview</span><span class=\"token punctuation\">(</span>blue<span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>view<span class=\"token punctuation\">.</span><span class=\"token function\">addSubview</span><span class=\"token punctuation\">(</span>yellow<span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>view<span class=\"token punctuation\">.</span><span class=\"token function\">addSubview</span><span class=\"token punctuation\">(</span>green<span class=\"token punctuation\">)</span>\n        <span class=\"token comment\">// Do any additional setup after loading the view, typically from a nib.</span>\n    <span class=\"token punctuation\">}</span>\n    \n    \n    <span class=\"token keyword\">override</span> <span class=\"token keyword\">func</span> <span class=\"token function\">didReceiveMemoryWarning</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">super</span><span class=\"token punctuation\">.</span><span class=\"token function\">didReceiveMemoryWarning</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n        <span class=\"token comment\">// Dispose of any resources that can be recreated.</span>\n    <span class=\"token punctuation\">}</span>\n\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>各 View を tap で”touched”とログを吐きます。\n\n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; margin-bottom: 1.0725rem;; max-width: 450px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 182.66666666666666%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"colorviewpng\" title=\"\" src=\"/static/colorview-7bbf126f6d8599e622daf220fc19fe77-5ecc7.png\" srcset=\"/static/colorview-7bbf126f6d8599e622daf220fc19fe77-39df4.png 188w,\n/static/colorview-7bbf126f6d8599e622daf220fc19fe77-e100e.png 375w,\n/static/colorview-7bbf126f6d8599e622daf220fc19fe77-5ecc7.png 450w\" sizes=\"(max-width: 450px) 100vw, 450px\">\n    </span>\n  </span>\n  </p>\n<h4>Delegateの準備</h4>\n<hr>\n<h5>処理を移譲する側の準備</h5>\n<p><code>タップされたViewが、ViewControllerに対して、自分がタップされたことを通知する</code><br>\nこれを Delegate を使って実装したいと思います。    </p>\n<ol>\n<li>Delegate の実態を宣言する</li>\n</ol>\n<p><code>ColorView.swift</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-swift\"><code><span class=\"token keyword\">import</span> <span class=\"token builtin\">UIKit</span>\n\nprotocol <span class=\"token builtin\">ColorViewDlegate</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">func</span> <span class=\"token function\">tapped</span><span class=\"token punctuation\">(</span>color<span class=\"token punctuation\">:</span> <span class=\"token builtin\">ColorView</span><span class=\"token punctuation\">.</span>colors<span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">ColorView</span><span class=\"token punctuation\">:</span> <span class=\"token builtin\">UIView</span><span class=\"token punctuation\">{</span>\n<span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span>\n</code></pre>\n      </div>\n<ol start=\"2\">\n<li>Delegate を宣言する</li>\n</ol>\n<p><code>ColorView.swift</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-swift\"><code><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span>\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">ColorView</span><span class=\"token punctuation\">:</span> <span class=\"token builtin\">UIView</span><span class=\"token punctuation\">{</span>\n\n    <span class=\"token keyword\">var</span> background<span class=\"token punctuation\">:</span> colors<span class=\"token operator\">?</span>\n    <span class=\"token keyword\">var</span> delegate<span class=\"token punctuation\">:</span> <span class=\"token builtin\">ColorViewDlegate</span><span class=\"token operator\">?</span>\n<span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span>\n</code></pre>\n      </div>\n<ol start=\"3\">\n<li>Delegate function を呼ぶ</li>\n</ol>\n<p><code>ColorView.swift</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-swift\"><code><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span>\n    <span class=\"token keyword\">override</span> <span class=\"token keyword\">func</span> <span class=\"token function\">touchesBegan</span><span class=\"token punctuation\">(</span><span class=\"token number\">_</span> touches<span class=\"token punctuation\">:</span> <span class=\"token builtin\">Set</span><span class=\"token operator\">&lt;</span><span class=\"token builtin\">UITouch</span><span class=\"token operator\">></span><span class=\"token punctuation\">,</span> with event<span class=\"token punctuation\">:</span> <span class=\"token builtin\">UIEvent</span><span class=\"token operator\">?</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n        <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"touched\"</span><span class=\"token punctuation\">)</span>\n        delegate<span class=\"token operator\">?</span><span class=\"token punctuation\">.</span><span class=\"token function\">tapped</span><span class=\"token punctuation\">(</span>color<span class=\"token punctuation\">:</span> <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>background<span class=\"token operator\">!</span><span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span>\n</code></pre>\n      </div>\n<p>上記 1~3 の手順で、通知する側(処理を移譲する側)の準備は終了です。</p>\n<h4>処理を移譲される側の準備</h4>\n<ol>\n<li>Delegate を使うことを定義</li>\n</ol>\n<p><code>ViewController.swift</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-swift\"><code><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span>\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">ViewController</span><span class=\"token punctuation\">:</span> <span class=\"token builtin\">UIViewController</span><span class=\"token punctuation\">,</span> <span class=\"token builtin\">ColorViewDlegate</span><span class=\"token punctuation\">{</span>\n<span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span>\n</code></pre>\n      </div>\n<ol start=\"2\">\n<li>Delegate を受けた時の処理</li>\n</ol>\n<p><code>ViewController.swift</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-swift\"><code><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span>\n    <span class=\"token keyword\">func</span> <span class=\"token function\">tapped</span><span class=\"token punctuation\">(</span>color<span class=\"token punctuation\">:</span> <span class=\"token builtin\">ColorView</span><span class=\"token punctuation\">.</span>colors<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">switch</span> color <span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span>red<span class=\"token punctuation\">:</span>\n            <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"red\"</span><span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span>blue<span class=\"token punctuation\">:</span>\n            <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"blue\"</span><span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span>yellow<span class=\"token punctuation\">:</span>\n            <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"yellow\"</span><span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span>green<span class=\"token punctuation\">:</span>\n            <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"green\"</span><span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span><span class=\"token keyword\">none</span><span class=\"token punctuation\">:</span>\n            <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"none\"</span><span class=\"token punctuation\">)</span>\n        <span class=\"token punctuation\">}</span>\n    <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span>\n</code></pre>\n      </div>\n<ol start=\"3\">\n<li>Delegate をの移譲先がこの Class であることの宣言</li>\n</ol>\n<p><code>ViewController.swift</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-swift\"><code>    red<span class=\"token punctuation\">.</span>delegate <span class=\"token operator\">=</span> <span class=\"token keyword\">self</span>\n    blue<span class=\"token punctuation\">.</span>delegate <span class=\"token operator\">=</span> <span class=\"token keyword\">self</span>\n    yellow<span class=\"token punctuation\">.</span>delegate <span class=\"token operator\">=</span> <span class=\"token keyword\">self</span>\n    green<span class=\"token punctuation\">.</span>delegate <span class=\"token operator\">=</span> <span class=\"token keyword\">self</span>\n</code></pre>\n      </div>\n<p>ここで、“self”とできるのは、1.で Delegate を使うことを定義してあるからです。</p>\n<h4>コード全体</h4>\n<p><code>ColorView.swift</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-swift\"><code><span class=\"token keyword\">import</span> <span class=\"token builtin\">UIKit</span>\n\nprotocol <span class=\"token builtin\">ColorViewDlegate</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">func</span> <span class=\"token function\">tapped</span><span class=\"token punctuation\">(</span>color<span class=\"token punctuation\">:</span> <span class=\"token builtin\">ColorView</span><span class=\"token punctuation\">.</span>colors<span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">ColorView</span><span class=\"token punctuation\">:</span> <span class=\"token builtin\">UIView</span> <span class=\"token punctuation\">{</span>\n\n    <span class=\"token keyword\">enum</span> colors<span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">case</span> red\n        <span class=\"token keyword\">case</span> blue\n        <span class=\"token keyword\">case</span> yellow\n        <span class=\"token keyword\">case</span> green\n        <span class=\"token keyword\">case</span> <span class=\"token keyword\">none</span>\n    <span class=\"token punctuation\">}</span>\n    \n    <span class=\"token keyword\">var</span> background<span class=\"token punctuation\">:</span> colors<span class=\"token operator\">?</span>\n    <span class=\"token keyword\">var</span> delegate<span class=\"token punctuation\">:</span> <span class=\"token builtin\">ColorViewDlegate</span><span class=\"token operator\">?</span>\n    \n    <span class=\"token keyword\">override</span> <span class=\"token keyword\">init</span><span class=\"token punctuation\">(</span>frame<span class=\"token punctuation\">:</span> <span class=\"token builtin\">CGRect</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">super</span><span class=\"token punctuation\">.</span><span class=\"token keyword\">init</span><span class=\"token punctuation\">(</span>frame<span class=\"token punctuation\">:</span> frame<span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">}</span>\n\n    <span class=\"token keyword\">public</span> <span class=\"token keyword\">func</span> <span class=\"token function\">setColor</span><span class=\"token punctuation\">(</span>color<span class=\"token punctuation\">:</span> colors<span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>background <span class=\"token operator\">=</span> color\n        <span class=\"token keyword\">switch</span> color <span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span>red<span class=\"token punctuation\">:</span>\n            <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>backgroundColor <span class=\"token operator\">=</span> <span class=\"token punctuation\">.</span>red\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span>blue<span class=\"token punctuation\">:</span>\n            <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>backgroundColor <span class=\"token operator\">=</span> <span class=\"token punctuation\">.</span>blue\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span>yellow<span class=\"token punctuation\">:</span>\n            <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>backgroundColor <span class=\"token operator\">=</span> <span class=\"token punctuation\">.</span>yellow\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span>green<span class=\"token punctuation\">:</span>\n            <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>backgroundColor <span class=\"token operator\">=</span> <span class=\"token punctuation\">.</span>green\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span><span class=\"token keyword\">none</span><span class=\"token punctuation\">:</span>\n            <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>backgroundColor <span class=\"token operator\">=</span> <span class=\"token punctuation\">.</span>black\n        <span class=\"token punctuation\">}</span>\n    <span class=\"token punctuation\">}</span>\n    \n    <span class=\"token keyword\">override</span> <span class=\"token keyword\">func</span> <span class=\"token function\">touchesBegan</span><span class=\"token punctuation\">(</span><span class=\"token number\">_</span> touches<span class=\"token punctuation\">:</span> <span class=\"token builtin\">Set</span><span class=\"token operator\">&lt;</span><span class=\"token builtin\">UITouch</span><span class=\"token operator\">></span><span class=\"token punctuation\">,</span> with event<span class=\"token punctuation\">:</span> <span class=\"token builtin\">UIEvent</span><span class=\"token operator\">?</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n        <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"touched\"</span><span class=\"token punctuation\">)</span>\n        delegate<span class=\"token operator\">?</span><span class=\"token punctuation\">.</span><span class=\"token function\">tapped</span><span class=\"token punctuation\">(</span>color<span class=\"token punctuation\">:</span> <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>background<span class=\"token operator\">!</span><span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">}</span>\n    \n    <span class=\"token keyword\">required</span> <span class=\"token keyword\">init</span><span class=\"token operator\">?</span><span class=\"token punctuation\">(</span>coder aDecoder<span class=\"token punctuation\">:</span> <span class=\"token builtin\">NSCoder</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n        <span class=\"token function\">fatalError</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"init(coder:) has not been implemented\"</span><span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">}</span>\n    \n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p><code>ViewController.swift</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-swift\"><code><span class=\"token keyword\">import</span> <span class=\"token builtin\">UIKit</span>\n\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">ViewController</span><span class=\"token punctuation\">:</span> <span class=\"token builtin\">UIViewController</span><span class=\"token punctuation\">,</span> <span class=\"token builtin\">ColorViewDlegate</span> <span class=\"token punctuation\">{</span>\n    \n    <span class=\"token comment\">//MARK: ColorViewDelegate function</span>\n    <span class=\"token keyword\">func</span> <span class=\"token function\">tapped</span><span class=\"token punctuation\">(</span>color<span class=\"token punctuation\">:</span> <span class=\"token builtin\">ColorView</span><span class=\"token punctuation\">.</span>colors<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">switch</span> color <span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span>red<span class=\"token punctuation\">:</span>\n            <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"red\"</span><span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span>blue<span class=\"token punctuation\">:</span>\n            <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"blue\"</span><span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span>yellow<span class=\"token punctuation\">:</span>\n            <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"yellow\"</span><span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span>green<span class=\"token punctuation\">:</span>\n            <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"green\"</span><span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">case</span> <span class=\"token punctuation\">.</span><span class=\"token keyword\">none</span><span class=\"token punctuation\">:</span>\n            <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"none\"</span><span class=\"token punctuation\">)</span>\n        <span class=\"token punctuation\">}</span>\n    <span class=\"token punctuation\">}</span>\n    \n    \n    <span class=\"token keyword\">override</span> <span class=\"token keyword\">func</span> <span class=\"token function\">viewDidLoad</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">super</span><span class=\"token punctuation\">.</span><span class=\"token function\">viewDidLoad</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">let</span> size <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>view<span class=\"token punctuation\">.</span>frame<span class=\"token punctuation\">.</span>width<span class=\"token operator\">*</span><span class=\"token number\">0.8</span><span class=\"token punctuation\">)</span><span class=\"token operator\">/</span><span class=\"token number\">2</span>\n        <span class=\"token keyword\">let</span> mergin <span class=\"token operator\">=</span> <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>view<span class=\"token punctuation\">.</span>frame<span class=\"token punctuation\">.</span>width<span class=\"token operator\">*</span><span class=\"token number\">0.1</span>\n        \n        <span class=\"token comment\">//Initialize Views</span>\n        <span class=\"token keyword\">let</span> red <span class=\"token operator\">=</span> <span class=\"token function\">ColorView</span><span class=\"token punctuation\">(</span>frame<span class=\"token punctuation\">:</span> <span class=\"token function\">CGRect</span><span class=\"token punctuation\">(</span>x<span class=\"token punctuation\">:</span> mergin<span class=\"token punctuation\">,</span> y<span class=\"token punctuation\">:</span> <span class=\"token number\">60</span><span class=\"token punctuation\">,</span> width<span class=\"token punctuation\">:</span> size<span class=\"token punctuation\">,</span> height<span class=\"token punctuation\">:</span> size<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">let</span> blue <span class=\"token operator\">=</span> <span class=\"token function\">ColorView</span><span class=\"token punctuation\">(</span>frame<span class=\"token punctuation\">:</span> <span class=\"token function\">CGRect</span><span class=\"token punctuation\">(</span>x<span class=\"token punctuation\">:</span> <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>view<span class=\"token punctuation\">.</span>frame<span class=\"token punctuation\">.</span>width<span class=\"token operator\">-</span><span class=\"token punctuation\">(</span>size<span class=\"token operator\">+</span>mergin<span class=\"token punctuation\">)</span><span class=\"token punctuation\">,</span> y<span class=\"token punctuation\">:</span> <span class=\"token number\">60</span><span class=\"token punctuation\">,</span> width<span class=\"token punctuation\">:</span> size<span class=\"token punctuation\">,</span> height<span class=\"token punctuation\">:</span> size<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">let</span> yellow <span class=\"token operator\">=</span> <span class=\"token function\">ColorView</span><span class=\"token punctuation\">(</span>frame<span class=\"token punctuation\">:</span> <span class=\"token function\">CGRect</span><span class=\"token punctuation\">(</span>x<span class=\"token punctuation\">:</span> mergin<span class=\"token punctuation\">,</span> y<span class=\"token punctuation\">:</span> <span class=\"token number\">60</span><span class=\"token operator\">+</span>size<span class=\"token punctuation\">,</span> width<span class=\"token punctuation\">:</span> size<span class=\"token punctuation\">,</span> height<span class=\"token punctuation\">:</span> size<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">let</span> green <span class=\"token operator\">=</span> <span class=\"token function\">ColorView</span><span class=\"token punctuation\">(</span>frame<span class=\"token punctuation\">:</span> <span class=\"token function\">CGRect</span><span class=\"token punctuation\">(</span>x<span class=\"token punctuation\">:</span> <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>view<span class=\"token punctuation\">.</span>frame<span class=\"token punctuation\">.</span>width<span class=\"token operator\">-</span><span class=\"token punctuation\">(</span>size<span class=\"token operator\">+</span>mergin<span class=\"token punctuation\">)</span><span class=\"token punctuation\">,</span> y<span class=\"token punctuation\">:</span> <span class=\"token number\">60</span><span class=\"token operator\">+</span>size<span class=\"token punctuation\">,</span> width<span class=\"token punctuation\">:</span> size<span class=\"token punctuation\">,</span> height<span class=\"token punctuation\">:</span> size<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n        \n        <span class=\"token comment\">//Set Color</span>\n        red<span class=\"token punctuation\">.</span><span class=\"token function\">setColor</span><span class=\"token punctuation\">(</span>color<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">.</span>red<span class=\"token punctuation\">)</span>\n        blue<span class=\"token punctuation\">.</span><span class=\"token function\">setColor</span><span class=\"token punctuation\">(</span>color<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">.</span>blue<span class=\"token punctuation\">)</span>\n        yellow<span class=\"token punctuation\">.</span><span class=\"token function\">setColor</span><span class=\"token punctuation\">(</span>color<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">.</span>yellow<span class=\"token punctuation\">)</span>\n        green<span class=\"token punctuation\">.</span><span class=\"token function\">setColor</span><span class=\"token punctuation\">(</span>color<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">.</span>green<span class=\"token punctuation\">)</span>\n        \n        <span class=\"token comment\">//Set delegate</span>\n        red<span class=\"token punctuation\">.</span>delegate <span class=\"token operator\">=</span> <span class=\"token keyword\">self</span>\n        blue<span class=\"token punctuation\">.</span>delegate <span class=\"token operator\">=</span> <span class=\"token keyword\">self</span>\n        yellow<span class=\"token punctuation\">.</span>delegate <span class=\"token operator\">=</span> <span class=\"token keyword\">self</span>\n        green<span class=\"token punctuation\">.</span>delegate <span class=\"token operator\">=</span> <span class=\"token keyword\">self</span>\n        \n        <span class=\"token comment\">//Add view</span>\n        <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>view<span class=\"token punctuation\">.</span><span class=\"token function\">addSubview</span><span class=\"token punctuation\">(</span>red<span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>view<span class=\"token punctuation\">.</span><span class=\"token function\">addSubview</span><span class=\"token punctuation\">(</span>blue<span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>view<span class=\"token punctuation\">.</span><span class=\"token function\">addSubview</span><span class=\"token punctuation\">(</span>yellow<span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>view<span class=\"token punctuation\">.</span><span class=\"token function\">addSubview</span><span class=\"token punctuation\">(</span>green<span class=\"token punctuation\">)</span>\n        <span class=\"token comment\">// Do any additional setup after loading the view, typically from a nib.</span>\n    <span class=\"token punctuation\">}</span>\n    \n    <span class=\"token keyword\">override</span> <span class=\"token keyword\">func</span> <span class=\"token function\">didReceiveMemoryWarning</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">super</span><span class=\"token punctuation\">.</span><span class=\"token function\">didReceiveMemoryWarning</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n        <span class=\"token comment\">// Dispose of any resources that can be recreated.</span>\n    <span class=\"token punctuation\">}</span>\n\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>これで、log が</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>touched\nred\ntouched\nblue\ntouched\nyellow\ntouched\n...</code></pre>\n      </div>\n<p>となれば成功です。</p>\n<h1>まとめ</h1>\n<p>全体の流れをもう一度確認すると。</p>\n<p>通知元のクラス(今回は ColorView)で、</p>\n<ul>\n<li><code>Delegateの実態</code></li>\n<li><code>Delegateの宣言</code></li>\n<li><code>Delegateの呼び出し</code><br>\nを準備する。　　</li>\n</ul>\n<p>通知先のクラス(今回は ViewController)で、</p>\n<ul>\n<li><code>Delegateを使うことを定義</code></li>\n<li><code>移譲されたDelegate funcionの処理</code></li>\n<li><code>Delegateを移譲する先がこのクラス(Viewcontroller)であることの宣言</code></li>\n</ul>\n<p>この二つの流れで、処理を移譲することができます。\nどうでしょうか。delegate は何かと使いやすいコールバックなので、ぜひ使っていきましょう。　　　</p>","frontmatter":{"layout":"post","title":"Swfit4でコールバック(2)","path":"/swift4_callback_02/","categories":["Swift","Swift4","Callback","コールバック"],"date":"2018/02/04"}}},{"post":{"html":"<p>Swift4 でコールバック処理を調べてみる。<br>\nSwift におけるコールバックのやり方は主に以下の 3 通りかと思われる。</p>\n<ul>\n<li>クロージャによるコールバック</li>\n<li>Delegate を使ったコールバック</li>\n<li>NotificationCenter を使ったコールバック</li>\n</ul>\n<p>今回は<code>クロージャ</code>によるコールバックについて。</p>\n<!--more-->\n<h3>クロージャとは</h3>\n<hr>\n<p><code>無名関数</code>や<code>ラムダ式</code>とも呼ばれる。<br>\nクロージャの基本形    </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-swift\"><code><span class=\"token punctuation\">{</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">[</span>parameters<span class=\"token punctuation\">]</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">-</span><span class=\"token operator\">></span> <span class=\"token punctuation\">[</span><span class=\"token keyword\">return</span> type<span class=\"token punctuation\">]</span> <span class=\"token keyword\">in</span>\n    <span class=\"token punctuation\">[</span>statements<span class=\"token punctuation\">]</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>これが基本形です。<br>\n</p>\n<h3>関数の引数に関数を渡す</h3>\n<hr>\n<p>色々なサイトで、上記の説明のまま、クロージャの説明を始めるのですが、私にはわかりにくかったので、私なりの説明をさせていただきます。<br>\nまず、コールバックの主要なやり方としては、関数の引数に関数を渡す方法がありますね。<br>\nちょっとずつ進めます。</p>\n<h5>1. ただの関数</h5>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-swift\"><code><span class=\"token keyword\">func</span> <span class=\"token function\">sayhello</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n    <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"hello\"</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span>\n<span class=\"token function\">sayhello</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token comment\">//\"hello\"</span>\n</code></pre>\n      </div>\n<h4>2. 引数あり関数</h4>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-swift\"><code><span class=\"token keyword\">let</span> str <span class=\"token operator\">=</span> <span class=\"token string\">\"hello\"</span>\n<span class=\"token keyword\">func</span> <span class=\"token function\">sayhello</span><span class=\"token punctuation\">(</span>string<span class=\"token punctuation\">:</span> <span class=\"token builtin\">String</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n    <span class=\"token function\">print</span><span class=\"token punctuation\">(</span>string<span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span>\n<span class=\"token function\">sayhello</span><span class=\"token punctuation\">(</span>string<span class=\"token punctuation\">:</span> str<span class=\"token punctuation\">)</span> <span class=\"token comment\">// \"hello\"</span>\n</code></pre>\n      </div>\n<h4>3. 戻り値あり関数</h4>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-swift\"><code><span class=\"token keyword\">let</span> str <span class=\"token operator\">=</span> <span class=\"token string\">\"hello\"</span>\n<span class=\"token keyword\">func</span> <span class=\"token function\">sayhello</span><span class=\"token punctuation\">(</span>string<span class=\"token punctuation\">:</span> <span class=\"token builtin\">String</span><span class=\"token punctuation\">)</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token builtin\">String</span><span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">return</span> <span class=\"token string\">\"say \"</span> <span class=\"token operator\">+</span> str\n<span class=\"token punctuation\">}</span>\n<span class=\"token keyword\">let</span> sayhelloString <span class=\"token operator\">=</span> <span class=\"token function\">sayhello</span><span class=\"token punctuation\">(</span>string<span class=\"token punctuation\">:</span> str<span class=\"token punctuation\">)</span>\n<span class=\"token function\">print</span><span class=\"token punctuation\">(</span>sayhelloString<span class=\"token punctuation\">)</span><span class=\"token comment\">// \"say hello\"</span>\n</code></pre>\n      </div>\n<p>ここまでは基礎的な関数の処理ですね。</p>\n<h4>4. 関数の引数が関数</h4>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-swift\"><code><span class=\"token keyword\">func</span> <span class=\"token function\">sayHello</span><span class=\"token punctuation\">(</span>callback<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">-</span><span class=\"token operator\">></span> <span class=\"token builtin\">Void</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n    <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"say\"</span><span class=\"token punctuation\">)</span>\n    <span class=\"token function\">callback</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n    <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"done\"</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span>\n<span class=\"token keyword\">func</span> <span class=\"token function\">callback</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n    <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"hello\"</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token function\">sayHello</span><span class=\"token punctuation\">(</span>callback<span class=\"token punctuation\">:</span> callback<span class=\"token punctuation\">)</span>\n<span class=\"token comment\">//\"say\"</span>\n<span class=\"token comment\">//\"hello\"</span>\n<span class=\"token comment\">//\"done\"</span>\n</code></pre>\n      </div>\n<p><code>失敗例</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-swift\"><code><span class=\"token keyword\">func</span> <span class=\"token function\">sayHello</span><span class=\"token punctuation\">(</span>callback<span class=\"token punctuation\">:</span> <span class=\"token builtin\">Void</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n    <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"say\"</span><span class=\"token punctuation\">)</span>\n    callback\n    <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"done\"</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span>\n<span class=\"token keyword\">func</span> <span class=\"token function\">callback</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n    <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"hello\"</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span>\n<span class=\"token function\">sayHello</span><span class=\"token punctuation\">(</span>callback<span class=\"token punctuation\">:</span> <span class=\"token function\">callback</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n<span class=\"token comment\">//\"hello\"</span>\n<span class=\"token comment\">//\"say\"</span>\n<span class=\"token comment\">//\"done\"</span>\n</code></pre>\n      </div>\n<p>上記失敗例は、引数に callback 関数を渡した時点で、callback<code>()</code>により、callback 関数を読んでしまっている。成功例では、callback 関数が sayHello 関数内で呼ばれている。\nprint の結果の順番を比べていただきたい。</p>\n<h5>5. 無名関数(クロージャ)を引数にする。</h5>\n<p>No.4 では、わざわざ callback 関数を宣言してから引数に渡している。<br>\nこの時、引数に渡す時に関数を宣言できたら…?    </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-swift\"><code><span class=\"token keyword\">func</span> <span class=\"token function\">sayHello</span><span class=\"token punctuation\">(</span>callback<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">-</span><span class=\"token operator\">></span> <span class=\"token builtin\">Void</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n    <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"say\"</span><span class=\"token punctuation\">)</span>\n    <span class=\"token function\">callback</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n    <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"done\"</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span>\n<span class=\"token function\">sayHello</span><span class=\"token punctuation\">(</span>callback<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"hey closure\"</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n<span class=\"token comment\">//\"say\"</span>\n<span class=\"token comment\">//\"hey closure\"</span>\n<span class=\"token comment\">//\"done\"</span>\n</code></pre>\n      </div>\n<p>。。。どうでしょうか?\nクロージャなんとなくわかったんじゃないでしょうか?\n<br>\n要は<br>\n<code>関数1(sayHello関数)の引数に関数2を渡す時に、関数2を宣言する</code><br>\n書き方のことです。(乱暴)</p>\n<h5>5.1 クロージャに引数があるパターン</h5>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-swift\"><code><span class=\"token keyword\">func</span> <span class=\"token function\">sayHello</span><span class=\"token punctuation\">(</span>callback<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">(</span><span class=\"token builtin\">String</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">-</span><span class=\"token operator\">></span> <span class=\"token builtin\">Void</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n    <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"say\"</span><span class=\"token punctuation\">)</span>\n    <span class=\"token function\">callback</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"引数\"</span><span class=\"token punctuation\">)</span>\n    <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"done\"</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span>\n\nsayHello <span class=\"token punctuation\">{</span> <span class=\"token punctuation\">(</span>text<span class=\"token punctuation\">)</span> <span class=\"token keyword\">in</span>\n    <span class=\"token function\">print</span><span class=\"token punctuation\">(</span>text<span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span>\n<span class=\"token comment\">//\"say\"</span>\n<span class=\"token comment\">//\"引数\"</span>\n<span class=\"token comment\">//\"done\"</span>\n</code></pre>\n      </div>\n<h4>5.2 クロージャに戻り値があるパターン</h4>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-swift\"><code><span class=\"token keyword\">func</span> <span class=\"token function\">sayHello</span><span class=\"token punctuation\">(</span>callback<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">(</span><span class=\"token builtin\">String</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">-</span><span class=\"token operator\">></span> <span class=\"token builtin\">Int</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n    <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"say\"</span><span class=\"token punctuation\">)</span>\n    <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token function\">callback</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"これは何文字ですか?\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n    <span class=\"token function\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"done\"</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span>\n\nsayHello <span class=\"token punctuation\">{</span> <span class=\"token punctuation\">(</span>text<span class=\"token punctuation\">)</span> <span class=\"token operator\">-</span><span class=\"token operator\">></span> <span class=\"token builtin\">Int</span> <span class=\"token keyword\">in</span>\n    <span class=\"token keyword\">return</span> text<span class=\"token punctuation\">.</span>characters<span class=\"token punctuation\">.</span><span class=\"token builtin\">count</span>\n<span class=\"token punctuation\">}</span>\n<span class=\"token comment\">//\"say\"</span>\n<span class=\"token comment\">//\"10\"</span>\n<span class=\"token comment\">//\"done\"</span>\n</code></pre>\n      </div>\n<h3>まとめ</h3>\n<hr>\n<p>どうでしょうか?\nこの流れを理解していればコードを読んでて、急にわからなくなる事が減ったり、関数がネストせずに書けたりするのではないでしょうか?<br>\n分からなくなったら落ち着いて、関数を二つ書いてしまえば、整理できるかもしれません。\n以上、Swift4 におけるクロージャについて基礎基礎でした。</p>","frontmatter":{"layout":"post","title":"Swift4でコールバック(1)","path":"/swift4_callback_01/","categories":["Swift","Swift4","callBack"],"date":"2018/01/23"}}},{"post":{"html":"<p>Swift4 Callback シリーズ。</p>\n<ul>\n<li>クロージャによるコールバック</li>\n<li>Delegate を使ったコールバック</li>\n<li>NotificationCenter を使ったコールバック</li>\n</ul>\n<p>今回は<code>NotificationCenter</code>によるコールバックについて。</p>\n<!--more-->\n<h1>NotificatioinCenter とは</h1>\n<hr>\n<blockquote>\n<p>The Notification Center framework helps you create and manage app extensions that implement widgets. The framework provides an API you can use to specify whether a widget has content to display, and to customize aspects of its appearance and behavior.</p>\n</blockquote>\n<p>おぉ。。。<br>\n<code>Notificaion Center framework</code>は</p>\n<ul>\n<li>ウィジット機能の拡張をを実装するアプリを作成、管理するのに役に立つ。</li>\n<li>ウィジェットに表示するコンテンツがあるかどうかを指定したり、その外観や動作をカスタマイズしたりするための API が用意されている。</li>\n</ul>\n<p>なるほど。わからん。<br>\n<a href=\"https://qiita.com/mono0926/items/754c5d2dbe431542c75e\">Qiitaの記事</a><br>\nこの記事が参考になりました。\nというか、この記事を読めば大体わかりますね。</p>\n<h1>前回のDelegateパターンをObserverパターンにできないか?</h1>\n<p>やってみましょう。<br>\n…\n一旦ストップ。</p>","frontmatter":{"layout":"post","title":"Swift4でコールバック(3)","path":"/swift4_callback_03/","categories":["Swift","Swift4","callBack"],"date":"2018/02/05"}}},{"post":{"html":"<p>Reacive Extentions の一つである UniRx を触ってみる。</p>\n<!--more-->\n<h2>UniRxとは?</h2>\n<hr>\n<p>neuecc さんによる Unity 用の Rx ライブらりなのだ(小並感)<br>\n<a href=\"https://github.com/neuecc/UniRx\">UniRxのgithub</a>\n以下、README.md を読んでいきましょう。<br>\n「UniRx って何?」て聞かれた時のために。</p>\n<h2>README.md (!原文ママ)</h2>\n<hr>\n<blockquote>\n<p>UniRx (Reactive Extensions for Unity) is a reimplementation of the .NET Reactive Extensions. The Official Rx implementation is great but doesn’t work on Unity and has issues with iOS IL2CPP compatibility.</p>\n</blockquote>\n<ul>\n<li>UniRxは .Net用のリアクティブエクステンションを再実装したものだと。</li>\n<li>公式の.Net用のも素晴らしいけど、Unityで動かないし、iOSと、IL2CPPでビルドした時に問題があるからね。</li>\n</ul>\n<blockquote>\n<p>Supported platforms are PC/Mac/Android/iOS/WP8/WindowsStore/etc and the library is fully supported on both Unity 5 and 4.6.</p>\n</blockquote>\n<ul>\n<li>Unityにフォーカスしてるし、いろんなプラットフォームをサポートしてるで。</li>\n</ul>\n<blockquote>\n<p>UniRx is available on the Unity Asset Store (FREE) - <a href=\"http://u3d.as/content/neuecc/uni-rx-reactive-extensions-for-unity/7tT\">http://u3d.as/content/neuecc/uni-rx-reactive-extensions-for-unity/7tT</a></p>\n</blockquote>\n<ul>\n<li>Asset storeから”無料で”入手可。</li>\n</ul>\n<p>うーんと、Reactive Extention がどういうものかは知ってる前提なんですかね?<br>\nちなみに、README はこの後サンプルが続きます。</p>\n<h2>Reactive Extentions とは</h2>\n<hr>\n<p> 困った時の wikipedia っすね。</p>\n<p> wikipedia をかいつまむと以下です。<br>\n</p>\n<ul>\n<li>オブザーバーパターンによる、非同期処理の API のことであると.</li>\n<li>非同期処理っつーのは,基本的には”call back”を持つもので、プログラマは関数の終了時にコールバックできるよね。</li>\n<li>この方法はオーバーヘッドを避けれるからいいよねーと。</li>\n</ul>\n<p><code>オブザーバーパターン</code>: 監視するパターン(Rx Extention においては、ストリームの監視な気がします。)<br>\n<code>オーバーヘッド</code>: 余分な処理による負荷</p>\n<ul>\n<li>\n<p>ストリームは以下の 3 つのイベント時に、それぞれの値を発行しますと。</p>\n<ul>\n<li>next</li>\n<li>error</li>\n<li>complete</li>\n</ul>\n</li>\n<li>\n<p>ストリームが購読(これがオブザーバーパターンたる所以ですかね?)されていない限りは、上記の値に対するイベントは発行されません。    </p>\n</li>\n</ul>\n<p>めっちゃ感覚的にかつ、小学生並みの感想といたしましては、    </p>\n<p><code>痒いところに手が届いて、言語仕様とマッチするコールバック、あ、これを求めてたんですよぉ</code><br>\n<br>\nと言いたいです。</p>\n<p>購読さえしていれば、next として任意の値をストリームに流すと、購読していたイベントを、任意のタイミングで、複数回呼べるのがとても便利です。</p>\n<p>次回から、コードをみていきましょう。</p>","frontmatter":{"layout":"post","title":"UniRxを触ってみる(1)","path":"/UniRx_01/","categories":["UniRx","C#"],"date":"2018/01/13"}}},{"post":{"html":"<p>今回から実際にコードを書いて動かしてみようと思います。</p>\n<!--more-->\n<h3>アセットストアからUniRxをインポート</h3>\n<hr>\n<p>アセットストアですぐに見つかります。\n<br>\n追加したら, <code>using</code>を忘れずに。</p>\n\n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; margin-bottom: 1.0725rem;; max-width: 750px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 77.45098039215686%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"using.png\" title=\"\" src=\"/static/using-7b7b992671d50c61a61b471187103cf1-245fd.png\" srcset=\"/static/using-7b7b992671d50c61a61b471187103cf1-7aee9.png 188w,\n/static/using-7b7b992671d50c61a61b471187103cf1-2014b.png 375w,\n/static/using-7b7b992671d50c61a61b471187103cf1-245fd.png 750w,\n/static/using-7b7b992671d50c61a61b471187103cf1-f687d.png 1020w\" sizes=\"(max-width: 750px) 100vw, 750px\">\n    </span>\n  </span>\n  \n<div class=\"gatsby-highlight\">\n      <pre class=\"language-csharp\"><code><span class=\"token keyword\">using</span> UniRx<span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<h3>Subjectを使ってみる。</h3>\n<hr>\n<p>ストリームソースとなりうるものです。\n初期化はこんな感じで,</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-csharp\"><code>Subject<span class=\"token operator\">&lt;</span>T<span class=\"token operator\">></span> hogehoge <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">Subject</span><span class=\"token operator\">&lt;</span>T<span class=\"token operator\">></span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>例えば    </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-csharp\"><code>Subject<span class=\"token operator\">&lt;</span><span class=\"token keyword\">string</span><span class=\"token operator\">></span> text <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">Subject</span><span class=\"token operator\">&lt;</span>stirng<span class=\"token operator\">></span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>上記の例だと、string の値が流れてくるストリームソースの作成に成功です。\n</p>\n<p>ストリームに値が流れたときの処理は,\n<br>\n<code>Subscribe();</code><br>\nを使います。\nストリームに値を流すときは,\n<br>\n<code>onNext();</code>\n<br>\nで流せます。</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-csharp\"><code><span class=\"token keyword\">using</span> UnityEngine<span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">using</span> UnityEngine<span class=\"token punctuation\">.</span>Events<span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">using</span> UniRx<span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">public</span> <span class=\"token keyword\">class</span> <span class=\"token class-name\">FirstSceneController</span> <span class=\"token punctuation\">:</span> MonoBehaviour\n<span class=\"token punctuation\">{</span>\n    Subject<span class=\"token operator\">&lt;</span><span class=\"token keyword\">string</span><span class=\"token operator\">></span> text <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">Subject</span><span class=\"token operator\">&lt;</span><span class=\"token keyword\">string</span><span class=\"token operator\">></span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token comment\">// Use this for initialization</span>\n    <span class=\"token keyword\">void</span> <span class=\"token function\">Start</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">{</span>\n        text\n            <span class=\"token punctuation\">.</span><span class=\"token function\">Subscribe</span><span class=\"token punctuation\">(</span>x <span class=\"token operator\">=</span><span class=\"token operator\">></span> Debug<span class=\"token punctuation\">.</span><span class=\"token function\">Log</span><span class=\"token punctuation\">(</span>x<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n        text<span class=\"token punctuation\">.</span><span class=\"token function\">OnNext</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"hello, Subject\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>Start 関数内で、購読の処理(subscribe)とストリームに値を流す処理を書いています。<br>\nこの後、onNext は値を変えたり、呼ぶタイミングを変えたりしながら、Subject が破棄されるまで何回でも呼ぶことができます。</p>\n<h3>click eventをストリームにしてみる。</h3>\n<hr>\n<p>UniRx を使えば、uGUI のクリック処理を以下のように書くことができます。</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-csharp\"><code><span class=\"token keyword\">using</span> UnityEngine<span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">using</span> UnityEngine<span class=\"token punctuation\">.</span>UI<span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">using</span> UniRx<span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">public</span> <span class=\"token keyword\">class</span> <span class=\"token class-name\">FirstSceneController</span> <span class=\"token punctuation\">:</span> MonoBehaviour\n<span class=\"token punctuation\">{</span>\n    <span class=\"token punctuation\">[</span>SerializeField<span class=\"token punctuation\">]</span> Button button01<span class=\"token punctuation\">;</span>\n    <span class=\"token comment\">// Use this for initialization</span>\n    <span class=\"token keyword\">void</span> <span class=\"token function\">Start</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">{</span>\n        button01<span class=\"token punctuation\">.</span><span class=\"token function\">OnClickAsObservable</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n            <span class=\"token punctuation\">.</span><span class=\"token function\">Subscribe</span><span class=\"token punctuation\">(</span>\n                _ <span class=\"token operator\">=</span><span class=\"token operator\">></span> Debug<span class=\"token punctuation\">.</span><span class=\"token function\">Log</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"clicked\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>ストリームに値が入っていないときは<br>\n<code>_ =></code><br>\nとするのが通例のようです。\nこれだけで、シーン上にあるボタンがクリックされたときに、ログを出力できます。\n<br>\n例えば、ログだけじゃなく、数行にわたる処理を書きたいときは,    </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-csharp\"><code><span class=\"token punctuation\">.</span><span class=\"token function\">Subscribe</span><span class=\"token punctuation\">(</span>_ <span class=\"token operator\">=</span><span class=\"token operator\">></span> Debug<span class=\"token punctuation\">.</span><span class=\"token function\">Log</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"clicked\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>の部分を,    </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-csharp\"><code><span class=\"token punctuation\">.</span><span class=\"token function\">Subscribe</span><span class=\"token punctuation\">(</span>_ <span class=\"token operator\">=</span><span class=\"token operator\">></span> <span class=\"token punctuation\">{</span>    \n    Debug<span class=\"token punctuation\">.</span><span class=\"token function\">Log</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"log1\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>    \n    Debug<span class=\"token punctuation\">.</span><span class=\"token function\">Log</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"log2\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>    \n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>としてあげれば複数行の処理を記述することができます。</p>","frontmatter":{"layout":"post","title":"Unirxを触ってみる(2)","path":"/UniRx_02/","categories":["C#","Unity","UniRx"],"date":"2018/01/22"}}}]}},"pathContext":{}}

/***/ })

});


